:root {
  --PrimaryColor: hsl(199, 100%, 33%);
  --SecondaryColor: hsl(187, 85%, 43%);
  --gradientColor: linear-gradient(to right, hsl(187, 85%, 43%), hsl(199, 100%, 33%));
  --whiteColor:hsl(0, 0%, 100%);
  --blackColor: hsl(201, 33%, 16%);
  --textColor:hsl(240, 4%, 36%);
  --whiteColorDeam:hsl(0, 0%, 93%);
  --greyText:rgb(190, 190, 190);
  --inputColor:rgb(239, 239, 239);
  --bodyColor:rgb(240, 240, 246);
  --cardBG:rgb(225, 225, 235);
}

.header {
  position: fixed;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: var(--whiteColor);
  width: 100%;
  padding: 1rem;
  z-index: 1000;
  box-shadow: 0 2px 4px rgba(2, 15, 29, 0.242);
}
.header .logo {
  color: var(--blackColor);
  font-weight: 600;
  cursor: pointer;
  align-items: center;
  justify-content: center;
}
.header .icon {
  font-size: 25px;
  color: var(--PrimaryColor);
  transform: translateY(5px);
}
@media screen and (max-width: 768px) {
  .header .navBar {
    position: absolute;
    border-radius: 1rem;
    background-color: var(--whiteColorDeam);
    height: -moz-max-content;
    height: max-content;
    padding: 1rem;
    width: 80%;
    top: -500rem;
    left: 50%;
    transform: translate(-53%);
    z-index: 2000;
    box-shadow: 0 2px 4px rgba(2, 15, 29, 0.904);
    transition: 0.3s ease-in-out;
  }
  .header .navBar .navBarLists {
    flex-direction: column;
    justify-content: center;
    width: 100%;
    margin: auto;
    padding: -5rem 0;
  }
  .header .navBar .navBarLists .navItem {
    padding: 0.5rem 0;
  }
  .header .navBar .navBarLists .navItem .navLinks {
    color: hsl(240, 4%, 36%);
    font-size: 0.9rem;
    font-weight: 600;
  }
  .header .navBar .navBarLists .navItem .navLinks:hover {
    color: hsl(240, 4%, 36%);
  }
  .header .navBar .navBarLists .btn {
    margin-top: 1rem;
  }
  .header .navBar .navBarLists .btn a {
    font-weight: 600;
    color: var(--textColor);
  }
  .header .navBar .closeNavBar {
    position: absolute;
    top: 1rem;
    right: 1.5rem;
    color: var(--PrimaryColor);
  }
  .header .navBar .closeNavBar:hover {
    color: var(--SecondaryColor);
  }
  .header .activeNavbar {
    top: 7rem;
  }
}
.header .toggleNavbar .icon {
  font-size: 25px;
  color: var(--PrimaryColor);
}
.header .toggleNavbar:hover.icon {
  color: var(--SecondaryColor);
}

.toggleNavBar,
.closeNavBar {
  display: block;
}

@media screen and (min-width: 770px) {
  .toggleNavBar,
  .closeNavBar {
    display: none;
  }
  .header {
    padding: 1.5rem 2rem;
  }
  .header .navBarLists {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: row;
  }
  .header .navBarLists .navItem .navLinks {
    color: var(--textColor);
    padding: 0 0.45rem;
    font-size: 0.9rem;
    font-weight: 500;
  }
  .header .navBarLists .navItem .navLinks:hover {
    color: var(--PrimaryColor);
  }
  .header .navBarLists .btn {
    margin-left: 1rem;
  }
  .header .navBarLists .btn a {
    color: var(--whiteColor);
    font-weight: 500;
  }
}
@media screen and (min-width: 960px) {
  .header .navBar .navBarLists .navItem {
    padding: 0 0.7rem;
  }
}
@media screen and (min-width: 1240px) {
  .header {
    padding: 1rem 8.5rem;
  }
}/*# sourceMappingURL=navbar.css.map */